import React, { PureComponent } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../../utils";
import { isTagShop, isTagEmbed, suiteTrack } from "../../../../../actions/api";
import Network from "../../../Elements/Network/Network";
import ShopButton from "../../../Elements/ShopButton/ShopButton";


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    if (suiteTrack) themeImpressionTracking(this.mediaHeight)
  }

  render() {
    const { itemData, itemIndex, wallID, ownerId, wall, isVisible, clickToShowPopUp, ThemeRule, personalization } = this.props;
    const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
    const cardStyle = { backgroundColor: ThemeRule.cardColor };
    const isShopBtn = isTagShop && (isProduct) && !ThemeRule.hideContent ? true : false;
    const isShopIcon = isTagShop && (isProduct && itemData.hotspot === 0) ? true : false
    const isHotspot = isTagShop && itemData.hotspot && isProduct ? true : false
    const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'


    return <div item-id={itemData.id} id={`tb-hc-post-${itemData.id}`} className="tb_c_r_post_wrapper" ref={this.mediaHeight} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} style={{ padding: wall.Personalization.padding / 2 }} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0}>
      <div className={`tb_c_r_post_in tb_icon_animate ${!isTagShop ? 'tb_c_r_post_in_animate' : ''}`} style={cardStyle} onClick={clickToShowPopUp(itemIndex, itemData)}>
        <div className="tb_c_r_post_media_wrapp">
          <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} preload={itemIndex == 0 ? true : false} isVisible={isVisible} />
        </div>
      </div>
    </div>
  }
}
