import React, { Component, memo } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from 'swiper/modules';
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/autoplay';
import 'swiper/css/free-mode';
import 'swiper/css/mousewheel';
import './scss/Reel.scss'

import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../../actions/themeActions'
import { isSocialEmbed, isTagShop } from '../../../../actions/api'
import { mainClass, detectMobile } from '../../../../utils'
class CReels extends Component {
  constructor(props) {
    super(props)
    this.inputRefs = {};
  }
  requestData = (swiper) => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps, loaderData } = this.props;
    if (swiper.progress > 0.8 && hasMoreData && !loaderData.isShowMoreLoading) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData, heightUpdate } = this.props;

    if (postData && postData.length == 1) {

      let arrow_left_id = document.querySelector(".tb_c_r_arrow_left__")
      let arrow_right_id = document.querySelector(".tb_c_r_arrow_right__")
      if (arrow_left_id && arrow_right_id) {
        document.querySelector(".tb_c_r_arrow_left__").style = 'display:none'
        document.querySelector(".tb_c_r_arrow_right__").style = 'display:none'
      }

    }
    setTimeout(() => heightUpdate(), 1000)

  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) return true
    else return false
  }



  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, parentID, keyRender } = this.props;
    const inputName = parentID;

    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slideDuration = wall.ThemeRule.slideDelay;
    const trimContent = wall.Personalization.trimcontent === 1 ? true : false;
    const manageItemCount = postData && postData.length ? detectMobile() ? 1 : postData.length > 3 ? 3 : postData.length : 3;
    const arrow = isSocialEmbed ? true : false
    return <><div className="tb_c_r_post_container">
      <Swiper
        key={keyRender}
        navigation={{
          enabled: arrow,
          nextEl: ".tb_c_r_arrow_right__",
          prevEl: ".tb_c_r_arrow_left__",
        }}
        ref={(parentID) => this.inputRefs[inputName] = parentID}
        style={{ width: '100%' }}
        // onScroll={(swiper) => this.requestData(swiper)}
        // onSlideChange={(swiper) => this.requestData(swiper)}
        // onAutoplay={(swiper) => this.requestData(swiper)}
        autoHeight={true}
        speed={2000}
        edgeSwipeDetection={true}
        slidesPerView={numberOfCoumn > 0 ? numberOfCoumn : manageItemCount}
        touchRatio={2}
        cssMode={isMobile}
        passiveListeners={true}
        height={'250px'}
        freeMode={{ enabled: false, sticky: false, momentumBounceRatio: 2, momentumVelocityRatio: 3 }}
        autoplay={{ delay: slideDuration ? slideDuration * 1000 : 5000, disableOnInteraction: false }}
        loop={!hasMoreData}
        breakpoints={{
          0: {
            slidesPerView: mobileColumn == 0 ? 1 : parseInt(mobileColumn)
          },
          480: {
            slidesPerView: mobileColumn == 0 ? 1 : parseInt(mobileColumn)
          },
          575: {
            slidesPerView: mobileColumn == 0 ? 2 : parseInt(mobileColumn)
          },
          767: {
            slidesPerView: numberOfCoumn == 0 ? 3 : parseInt(numberOfCoumn)
          },
          1024: {
            slidesPerView: numberOfCoumn == 0 ? 5 : parseInt(numberOfCoumn)
          },
          1400: {
            slidesPerView: numberOfCoumn == 0 ? 6 : parseInt(numberOfCoumn)
          },
          1600: {
            slidesPerView: numberOfCoumn == 0 ? 7 : parseInt(numberOfCoumn)
          }
        }}
        //mousewheel={{ forceToAxis: true, sensitivity: 2 }}
        watchSlidesProgress={true}
        modules={[Autoplay, Mousewheel, FreeMode, Navigation]} className={`tb_c_r_post_slider ${trimContent ? 'tb_c_r_fixed_height' : ''}`}>
        <>
          {
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return <SwiperSlide style={{ margin: 0, padding: 0, }} key={index} p_id={parentID} className={`tb_${cardData.id} ${arrow ? '' : 'swiper-no-swiping'} `}>
                {({ isVisible }) => <Card isVisible={isVisible} ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} wall={wall} />}
              </SwiperSlide>
            })}
        </>
        {arrow ? <>
          <div className="tb_c_r_arrow tb_c_r_arrow_left__ tb__icon tb-arrow-left-alt" ><div></div></div>
          <div className="tb_c_r_arrow tb_c_r_arrow_right__ tb__icon tb-arrow-right-alt" ><div></div></div>
        </> : null}
      </Swiper>
    </div></>
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(CReels));