import React, { Suspense } from "react";
import { isSocialEmbed } from "../../../../../actions/api";
import Image from "../../../Elements/Media/Image";
import Video from "../../../Elements/Media/Video";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Media = ({ itemData, wallID, wall, ownerId, isVisible }) => {
  const isVideo = (itemData.type === 3 || itemData.type === 5) && isVisible ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  return <div className="tb_c_r_media_wrap">
 {isSocialEmbed ? <Suspense fallback={<></>}><Like postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {isSocialEmbed && wall.Personalization.interactive ? <MediaIcon itemData={itemData} IconClass="tb_c_r_media_icon" show={isSocialEmbed} hideVideo={true} />: null}
    {isVideo || autoPlay ?
      <Video VideoClass="tb_c_r_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} isCover={true} muted={true} controls={false} autoPlay={autoPlay} />
      :
      <Image ImageClass="tb_c_r_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} />
    }
  </div>
}
export default Media